import React from "react";
import logo from "./img/logo.svg"
import { motion } from "framer-motion"
import { Formik, Form } from 'formik';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
const baseUrl = "https://lilith-server.glitch.me"

export const HeaderComponent = ({ formAccept, setFormAccept }) => {
    const sendEmail = async (values, { setSubmitting }) => {
        try {
            let dataSend = {
                name: values.name,
                telegram: values.telegram,
            };

            const res = await fetch(`${baseUrl}/email/sendEmail`, {
                method: "POST",
                body: JSON.stringify(dataSend),
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                },
            });

            if (res.ok) {
                setFormAccept(true)
            } else {
                console.error(`: ${res.status} - ${res.statusText}`);
            }
        } catch (error) {
            console.error(":", error);
        } finally {
            setSubmitting(false);
        }
    };
    const validate = (values) => {
        const errors = {};

        // Validate name
        if (!values.name) {
            errors.name = '   ';
        } else if (!/^[A-Z-][a-z-]{0,14}$/.test(values.name)) {
            errors.name = '          15 ';
        }

        // Validate telegram
        if (!values.telegram) {
            errors.telegram = '   ';
        } else if (!/^@([a-z-0-9_]){1,}$/.test(values.telegram)) {
            errors.telegram = '    @    ';
        }

        return errors;
    };
    return (
        <header className="header">
            <div className="header__container">
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 0.5, delay: 0.1 }}
                    className="header__logo">
                    <a href="/">
                        <img className="header__logo-svg" src={logo} alt="" />
                    </a>
                </motion.div>
                <div className="header-block">
                    <h1 className="header-block__title">
                        <motion.span
                            initial={{ opacity: 0, x: -100 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.5, delay: 0.3 }}>
                            LILITH
                        </motion.span>
                        <motion.span
                            initial={{ opacity: 0, x: -100 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.5, delay: 0.6 }}>
                            AGENCY
                        </motion.span>
                    </h1>
                    <motion.h2
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ duration: 1, delay: 0.9 }}
                        className="header-block__text">Beauty meets success</motion.h2>
                </div>
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 0.5, delay: 1.2 }}
                    className="header-block__container">
                    <Formik validate={validate} onSubmit={sendEmail} initialValues={{ name: "", telegram: "" }}>
                        {({
                            values,
                            errors,
                            touched,
                            handleChange,
                            handleBlur,
                            isSubmitting,
                            handleSubmit,
                        }) => (
                            <Form style={{ height: "100%" }}>
                                <div className="header-register">
                                    <div className="header-register__block">
                                        <motion.h2
                                            initial={{ opacity: 0, x: 50 }}
                                            animate={{ opacity: 1, x: 0 }}
                                            transition={{ duration: 0.5, delay: 1.3 }}
                                            className="header-register__title footer-register__title">
                                             
                                        </motion.h2>
                                        <motion.div
                                            initial={{ opacity: 0, x: 50 }}
                                            animate={{ opacity: 1, x: 0 }}
                                            transition={{ duration: 0.5, delay: 1.5 }}
                                            className="header-register__register-block footer-register__register-block register-block">
                                            <TextField
                                                label=""
                                                variant="filled"
                                                className={formAccept ? "register-block__field register-block__field-name accept" : "register-block__field register-block__field-name"}
                                                type="name"
                                                name="name"
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.name}
                                                error={touched.name && Boolean(errors.name)}
                                                helperText={touched.name && errors.name}
                                            />
                                            <TextField
                                                label=""
                                                variant="filled"
                                                className={formAccept ? "register-block__field register-block__field-name accept" : "register-block__field register-block__field-name"}
                                                type="telegram"
                                                name="telegram"
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.telegram}
                                                error={touched.telegram && Boolean(errors.telegram)}
                                                helperText={touched.telegram && errors.telegram}
                                            />
                                            <Button
                                                type="submit"
                                                className={formAccept ? "register-block__button accept" : "register-block__button"}
                                                color={formAccept ? "success" : "primary"}
                                                variant="contained"
                                                fullWidth
                                            >
                                                {isSubmitting ? '...' : formAccept ? "" : ''}
                                            </Button>
                                        </motion.div>
                                    </div>
                                    <div className="footer-social">
                                        <motion.a
                                            initial={{ opacity: 0, y: 50 }}
                                            animate={{ opacity: 1, y: 0 }}
                                            transition={{ duration: 0.5, delay: 1.7 }}
                                            target="_blank"
                                            href="mailto:Nonenone2411@gmail.com" className="footer-social__gmail">
                                            <h2 className="footer-social__gmail-title">Gmail</h2><div className="footer-social__gmail-text">
                                                Nonenone2411@gmail.com
                                            </div>
                                        </motion.a>
                                        <motion.a
                                            initial={{ opacity: 0, y: 50 }}
                                            animate={{ opacity: 1, y: 0 }}
                                            transition={{ duration: 0.5, delay: 1.7 }}
                                            target="_blank"
                                            href="https://t.me/lilith_agency" className="footer-social__telegram">
                                            <h2 className="footer-social__telegram-title">Telegram</h2><div className="footer-social__telegram-text">
                                                @lilith_agency
                                            </div>
                                        </motion.a>
                                    </div>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </motion.div>
            </div>
        </header >
    )
}