import React from "react";
import phoneThree from "./img/bg-registration.webp"
import { Formik, Form } from 'formik';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { motion } from "framer-motion"
import { useInView } from "react-intersection-observer";
const baseUrl = "https://lilith-server.glitch.me"
export const FooterComponent = ({ formAccept, setFormAccept }) => {
    const [footerRef, footerInView] = useInView({
        triggerOnce: true,
        rootMargin: "-200px 0px",
    });
    const [socialRef, socialInView] = useInView({
        triggerOnce: true,
        rootMargin: "-80px 0px",
    });
    const sendEmail = async (values, { setSubmitting }) => {
        try {
            let dataSend = {
                name: values.name,
                telegram: values.telegram,
            };

            const res = await fetch(`${baseUrl}/email/sendEmail`, {
                method: "POST",
                body: JSON.stringify(dataSend),
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                },
            });

            if (res.ok) {
                setFormAccept(true)
            } else {
                console.error(`: ${res.status} - ${res.statusText}`);
            }
        } catch (error) {
            console.error(":", error);
        } finally {
            setSubmitting(false);
        }
    };
    const validate = (values) => {
        const errors = {};

        // Validate name
        if (!values.name) {
            errors.name = '   ';
        } else if (!/^[A-Z-][a-z-]{0,14}$/.test(values.name)) {
            errors.name = '          15 ';
        }

        // Validate telegram
        if (!values.telegram) {
            errors.telegram = '   ';
        } else if (!/^@([a-z-0-9_]){1,}$/.test(values.telegram)) {
            errors.telegram = '    @    ';
        }

        return errors;
    };
    return (<footer className="footer">
        <div className="footer__container">
            <Formik validate={validate} onSubmit={sendEmail} initialValues={{ name: "", telegram: "" }}>
                {({
                    values,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    isSubmitting,
                    handleSubmit,
                }) => (
                    <Form>
                        <div className="footer-register">
                            <div className="footer-register__content">
                                <motion.div
                                    initial={{ opacity: 0, x: -100 }}
                                    animate={footerInView ? { opacity: 1, x: 0 } : {}}
                                    transition={{ duration: 0.5, delay: 0.3 }}
                                    className="footer-register__img"><img src={phoneThree} alt=" " /></motion.div>
                            </div>
                            <div className="footer-register__block">
                                <motion.h2
                                    ref={footerRef}
                                    initial={{ opacity: 0, y: -100 }}
                                    animate={footerInView ? { opacity: 1, y: 0 } : {}}
                                    transition={{ duration: 0.5, delay: 0.1 }}
                                    className="footer-register__title"> </motion.h2>
                                <motion.div
                                    initial={{ opacity: 0, y: 100 }}
                                    animate={footerInView ? { opacity: 1, y: 0 } : {}}
                                    transition={{ duration: 0.5, delay: 0.1 }}
                                    className="footer-register__register-block register-block">
                                    <TextField
                                        label=""
                                        variant="filled"
                                        className={formAccept ? "register-block__field register-block__field-name accept" : "register-block__field register-block__field-name"}
                                        type="name"
                                        name="name"
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.name}
                                        error={touched.name && Boolean(errors.name)}
                                        helperText={touched.name && errors.name}
                                    />
                                    <TextField
                                        label=""
                                        variant="filled"
                                        className={formAccept ? "register-block__field register-block__field-name accept" : "register-block__field register-block__field-name"}
                                        type="telegram"
                                        name="telegram"
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.telegram}
                                        error={touched.telegram && Boolean(errors.telegram)}
                                        helperText={touched.telegram && errors.telegram}
                                    />
                                    <Button
                                        type="submit"
                                        className={formAccept ? "register-block__button accept" : "register-block__button"}
                                        color={formAccept ? "success" : "primary"}
                                        variant="contained"
                                        fullWidth
                                    >
                                        {isSubmitting ? '...' : formAccept ? "" : ''}
                                    </Button>
                                </motion.div>
                                <div className="footer-social">
                                    <motion.a
                                        ref={socialRef}
                                        initial={{ opacity: 0, y: -50 }}
                                        animate={socialInView ? { opacity: 1, y: 0 } : {}}
                                        transition={{ duration: 0.5 }}
                                        target="_blank"
                                        href="mailto:Nonenone2411@gmail.com" className="footer-social__gmail">
                                        <h2 className="footer-social__gmail-title">Gmail</h2><div className="footer-social__gmail-text">
                                            Nonenone2411@gmail.com
                                        </div>
                                    </motion.a>
                                    <motion.a
                                        initial={{ opacity: 0, y: -50 }}
                                        animate={socialInView ? { opacity: 1, y: 0 } : {}}
                                        transition={{ duration: 0.5 }}
                                        target="_blank"
                                        href="https://t.me/lilith_agency" className="footer-social__telegram">
                                        <h2 className="footer-social__telegram-title">Telegram</h2><div className="footer-social__telegram-text">
                                            @lilith_agency
                                        </div>
                                    </motion.a>
                                </div>
                            </div>
                        </div>
                    </Form>
                )}
            </Formik>
            <div className="copyright">copyright  2023</div>
        </div>
    </footer>)
}